<template>
  <div class="newsDetails">
    <div class="news-bar">
      <van-nav-bar title="新闻详情" left-arrow @click-left="linkHome"/>
    </div>
    <div class="newsDetails-top">
      <div class="newsDetails-title">隔夜外盘：欧股普涨美股收跌 纳指跌超1%失守 8000点关口</div>
      <div class="newsDetails-source">来源：第一财经</div>
      <div class="newsDetails-time">2019-09-28 06:40</div>
    </div>
    <div class="newsDetails-content">内容</div>
  </div>
</template>

<script>
export default {
  name: 'newsDetails',
  methods: {
    linkHome () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../style/theme";
.newsDetails{
  width: 100%;
  height: 100%;
  background-color: #f4f4f4;
  position: absolute;
  z-index: 9;
  .news-bar{
    border-bottom: tovw(1) solid #D5D3D3;
    .van-nav-bar{
      width: 100%;
      height: tovw(45);
      line-height: tovw(45);
    }
    .van-nav-bar__title{
      font-size: tovw(18)
    }
    .van-nav-bar .van-icon{
      color: $tFont;
    }
    .van-nav-bar__left{
      font-size: tovw(18);
    }
    .van-nav-bar__arrow{
      font-size: tovw(20);
      line-height: tovw(45);
    }
  }
  .newsDetails-top{
    height: tovw(74);
    padding: tovw(8) tovw(20);
    .newsDetails-title{
      width: 100%;
      height: tovw(42);
      font-size: tovw(16);
      margin-bottom: tovw(8);
      color: #2f2f2f;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
    .newsDetails-source{
      width: 50%;
      display: inline-block;
      font-size: tovw(12);
      color: $tFont;
    }
    .newsDetails-time{
      width: 50%;
      font-size: tovw(12);
      color: $tFont;
      display: inline-block;
      float: right;
      text-align: right;
    }
  }
  .newsDetails-content{
    background-color: #fff!important;
    padding: tovw(8) tovw(20);
  }
}
</style>



